<template>
  <div>
    <!-- <mu-appbar style="width: 100%;" class="header backgroundNaviBar">
      <a @click="()=>{
          $router.go(-1)
        }">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span style="color:white">标准值设定</span>
    </mu-appbar>-->
    <van-nav-bar
      title="标准值设定"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="filter-o" slot="right" size="20" /> -->
    </van-nav-bar>
    <div class="contentFactor">
      <div>
        <!-- <van-stepper v-model="value" min="0" max="14" /> -->
        <!-- <van-stepper v-model="value" step="0.2" :decimal-length="1" /> -->

        <div class="flexContent">
          <div>
            <span>pH标准值(酸)</span>
          </div>
          <div>
            <van-stepper v-model="$store.state.factor.ph" min="0" max="7" />
          </div>
        </div>
        <div class="flexContent">
          <div>
            <span>pH标准值(碱)</span>
          </div>
          <div>
            <van-stepper v-model="$store.state.factor.ph1" min="7" max="14" />
          </div>
        </div>
        <div class="flexContent">
          <div>
            <span>CODcr标准值</span>
          </div>
          <div>
            <van-stepper v-model="$store.state.factor.cod" min="0" max="400" />
          </div>
        </div>
        <div class="flexContent">
          <div>
            <span>氨氮标准值</span>
          </div>
          <div>
            <van-stepper v-model="$store.state.factor.ammoniaNitrogen" min="0" max="60" />
          </div>
        </div>
        <div class="flexContent">
          <div>
            <span>总氮标准值</span>
          </div>
          <div>
            <van-stepper v-model="$store.state.factor.nitrogen" min="0" max="60" />
          </div>
        </div>
        <div class="flexContent">
          <div>
            <span>总磷标准值</span>
          </div>
          <div>
            <van-stepper v-model="$store.state.factor.phosphorus" min="0" max="10" />
          </div>
        </div>
        <div class="flexContent">
          <div>
            <span>总铜标准值</span>
          </div>
          <div>
            <van-stepper v-model="$store.state.factor.cuprum" min="0" max="10" />
          </div>
        </div>
        <div class="flexContent">
          <div>
            <span>总镍标准值</span>
          </div>
          <div>
            <van-stepper
              v-model="$store.state.factor.nickel"
              step="0.1"
              :decimal-length="1"
              min="0"
              max="5"
            />
          </div>
        </div>
        <div class="flexContent">
          <div>
            <span>总铬标准值</span>
          </div>
          <div>
            <van-stepper
              v-model="$store.state.factor.chromium"
              step="0.1"
              :decimal-length="1"
              min="0"
              max="5"
            />
          </div>
        </div>
        <div class="flexContent">
          <div>
            <span>总氰化物标准值</span>
          </div>
          <div>
            <van-stepper
              v-model="$store.state.factor.prussiate"
              step="0.1"
              :decimal-length="1"
              min="0"
              max="5"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      value: 6
    };
  }
};
</script>
<style scoped>
.flexContent {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  padding: 5px 10px;
  background-color: white;
}
</style>


